import { NavLink } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.min.js';

const navItems = [
  { label: 'Home', to: '/' },
  { label: 'Login', to: '/login' },
  { label: 'Programing language', to: '/prog' },
  {
    label: 'AI',
    dropdown: [
      { label: 'Gemini', to: '/gemini' },
      { label: 'Wikipedia', to: '/wiki' },
      { label: 'Youtube', to: '/youtube' },
    ],
  },
];

function Navbar() {
  const renderNavItem = (item, isMobile = false) => {
    if (item.dropdown) {
      // Dropdown
      const dropdownId = isMobile
        ? 'mobileDropdown' + item.label
        : 'desktopDropdown' + item.label;
      return (
        <li key={item.label} className='nav-item dropdown'>
          <a
            href='#'
            className={`nav-link dropdown-toggle ${
              isMobile ? 'text-white' : ''
            }`}
            id={dropdownId}
            role='button'
            data-bs-toggle='dropdown'
            aria-expanded='false'
          >
            {item.label}
          </a>
          <ul
            className={`dropdown-menu ${isMobile ? '' : 'dropdown-menu-end'}`}
            aria-labelledby={dropdownId}
          >
            {item.dropdown.map((sub) => (
              <li key={sub.label}>
                <NavLink
                  to={sub.to}
                  className={`dropdown-item${isMobile ? '' : ''}`}
                  {...(isMobile ? { 'data-bs-dismiss': 'offcanvas' } : {})}
                >
                  {sub.label}
                </NavLink>
              </li>
            ))}
          </ul>
        </li>
      );
    }

    // Normal NavLink
    return (
      <li key={item.label} className='nav-item'>
        <NavLink
          to={item.to}
          className={`nav-link${isMobile ? ' text-white' : ''}`}
          activeclassname='active'
          {...(isMobile ? { 'data-bs-dismiss': 'offcanvas' } : {})}
          exact={item.to === '/'}
        >
          {item.label}
        </NavLink>
      </li>
    );
  };

  return (
    <>
      {/* Main Navbar */}
      <nav className='navbar navbar-expand-lg navbar-dark bg-primary px-4'>
        <NavLink className='navbar-brand' to='/'>
          <h1 className='h4 m-0'>Ai Model</h1>
        </NavLink>

        <button
          className='navbar-toggler'
          type='button'
          data-bs-toggle='offcanvas'
          data-bs-target='#mobileMenu'
          aria-controls='mobileMenu'
        >
          <span className='navbar-toggler-icon'></span>
        </button>

        {/* Desktop Menu */}
        <div
          className='collapse navbar-collapse d-none d-lg-flex'
          id='navbarNavDropdown'
        >
          <ul className='navbar-nav ms-auto d-flex align-items-center gap-3'>
            {navItems.map((item) => renderNavItem(item, false))}
          </ul>
        </div>
      </nav>

      {/* Offcanvas Menu for Small Devices */}
      <div
        className='offcanvas offcanvas-start bg-primary text-white'
        tabIndex='-1'
        id='mobileMenu'
        aria-labelledby='mobileMenuLabel'
      >
        <div className='offcanvas-header'>
          <h5 className='offcanvas-title' id='mobileMenuLabel'>
            Menu
          </h5>
          <button
            type='button'
            className='btn-close btn-close-white'
            data-bs-dismiss='offcanvas'
            aria-label='Close'
          ></button>
        </div>
        <div className='offcanvas-body'>
          <ul className='navbar-nav gap-2'>
            {navItems.map((item) => renderNavItem(item, true))}
          </ul>
        </div>
      </div>
    </>
  );
}

export default Navbar;
